<template>
  <div class="quiz-taking">
    <div class="quiz-header">
      <span>Q: {{ current + 1 }}/{{ questions.length }}</span>
      <span class="timer">{{ timer }}</span>
    </div>
    <div class="question-statement">
      <h3>{{ questions[current].title }}</h3>
      <div v-for="(option, idx) in questions[current].options" :key="idx">
        <label>
          <input type="radio" :value="idx+1" v-model="selectedOption" />
          {{ option }}
        </label>
      </div>
    </div>
    <div class="actions">
      <button @click="saveAndNext">Save and Next</button>
      <button @click="submit">Submit</button>
    </div>
  </div>
</template>
<script setup>
import { ref, onMounted } from 'vue';
const questions = ref([
  { title: 'What is 2+2?', options: ['1', '4', '3', '2'] },
  { title: 'What is H2O?', options: ['Water', 'Oxygen', 'Hydrogen', 'Helium'] }
]);
const current = ref(0);
const selectedOption = ref(null);
const timer = ref('00:30:00');
const saveAndNext = () => { if (current.value < questions.value.length - 1) { current.value++; selectedOption.value = null; } };
const submit = () => { alert('Quiz submitted!'); };
onMounted(() => { /* Implement timer logic here */ });
</script>
<style scoped>
.quiz-taking { max-width: 600px; margin: 2rem auto; background: #fff; padding: 2rem; border-radius: 8px; }
.quiz-header { display: flex; justify-content: space-between; margin-bottom: 1rem; }
.timer { font-weight: bold; color: #e67e22; }
.question-statement { margin-bottom: 1rem; }
.actions { display: flex; gap: 1rem; }
</style> 