import axios from 'axios';

const API_URL = 'http://localhost:5000/api/admin';

export const fetchSubjects = async () => {
  try {
    const response = await axios.get(`${API_URL}/subjects`, { withCredentials: true });
    return response.data;
  } catch (error) {
    throw error.response.data;
  }
};

export const createSubject = async (subjectData) => {
  try {
    const response = await axios.post(`${API_URL}/subjects`, subjectData, { withCredentials: true });
    return response.data;
  } catch (error) {
    throw error.response.data;
  }
};

export const updateSubject = async (id, subjectData) => {
  try {
    const response = await axios.put(`${API_URL}/subjects/${id}`, subjectData, { withCredentials: true });
    return response.data;
  } catch (error) {
    throw error.response.data;
  }
};

export const deleteSubject = async (id) => {
  try {
    const response = await axios.delete(`${API_URL}/subjects/${id}`, { withCredentials: true });
    return response.data;
  } catch (error) {
    throw error.response.data;
  }
};