<template>
  <div class="quiz-management">
    <h2>Quiz Management</h2>
    <table>
      <thead>
        <tr><th>ID</th><th>Title</th><th>Actions</th></tr>
      </thead>
      <tbody>
        <tr v-for="quiz in quizzes" :key="quiz.id">
          <td>{{ quiz.id }}</td>
          <td>{{ quiz.title }}</td>
          <td>
            <button @click="editQuiz(quiz)">Edit</button>
            <button @click="deleteQuizHandler(quiz.id)">Delete</button>
          </td>
        </tr>
      </tbody>
    </table>
    <button @click="showAddQuiz = true">+ Quiz</button>
    <div v-if="showAddQuiz" class="modal">
      <div class="modal-content">
        <h3>New Quiz</h3>
        <input v-model="newQuiz.title" placeholder="Quiz Title" />
        <input v-model="newQuiz.date" type="date" placeholder="Date" />
        <input v-model="newQuiz.duration" placeholder="Duration (min)" />
        <button @click="addQuiz">Save</button>
        <button @click="showAddQuiz = false">Cancel</button>
      </div>
    </div>
  </div>
</template>
<script setup>
import { ref, onMounted } from 'vue';
import axios from 'axios';
const quizzes = ref([]);
const showAddQuiz = ref(false);
const newQuiz = ref({ title: '', date: '', duration: '' });
const loadQuizzes = async () => {
  try {
    const res = await axios.get('http://localhost:5000/api/quizzes');
    quizzes.value = res.data;
  } catch (e) {
    alert('Failed to load quizzes');
  }
};
const addQuiz = async () => {
  if (newQuiz.value.title) {
    await axios.post('http://localhost:5000/api/quizzes', newQuiz.value);
    newQuiz.value = { title: '', date: '', duration: '' };
    showAddQuiz.value = false;
    loadQuizzes();
  }
};
const editQuiz = (quiz) => {
  // Implement edit modal if needed
  alert('Edit quiz: ' + quiz.title);
};
const deleteQuizHandler = async (id) => {
  await axios.delete(`http://localhost:5000/api/quizzes/${id}`);
  loadQuizzes();
};
onMounted(loadQuizzes);
</script>
<style scoped>
.quiz-management { max-width: 700px; margin: 2rem auto; background: #fff; padding: 2rem; border-radius: 8px; }
table { width: 100%; border-collapse: collapse; margin-bottom: 1rem; }
th, td { border: 1px solid #ddd; padding: 0.5rem; text-align: left; }
button { margin-right: 0.5rem; }
.modal { position: fixed; top: 0; left: 0; right: 0; bottom: 0; background: rgba(0,0,0,0.3); display: flex; align-items: center; justify-content: center; }
.modal-content { background: #fff; padding: 2rem; border-radius: 8px; min-width: 300px; display: flex; flex-direction: column; gap: 1rem; }
</style>