<template>
  <div class="admin-dashboard">
    <header class="admin-header">
      <nav class="main-nav">
        <router-link to="/admin">Home</router-link>
        <router-link to="/admin/quiz">Quiz</router-link>
        <router-link to="/admin/summary">Summary</router-link>
        <button @click="logout" class="logout-btn">Logout</button>
      </nav>
      <div class="admin-search">
        <input type="text" placeholder="Search..." v-model="searchQuery">
        <button @click="search">Search</button>
      </div>
      <div class="welcome-message">Welcome Admin</div>
    </header>
    <main class="dashboard-content">
      <section class="subject-section">
        <h2>Subjects</h2>
        <div v-for="subject in subjects" :key="subject.id" class="subject-card">
          <div><strong>{{ subject.name }}</strong></div>
          <div>{{ subject.description }}</div>
          <button @click="editSubject(subject)">Edit</button>
          <button @click="deleteSubject(subject.id)">Delete</button>
        </div>
        <button @click="showAddSubject = true">+ Subject</button>
      </section>
      <section class="chapter-section">
        <h2>Chapters</h2>
        <div v-for="chapter in chapters" :key="chapter.id" class="chapter-card">
          <div><strong>{{ chapter.name }}</strong></div>
          <div>{{ chapter.description }}</div>
          <button @click="editChapter(chapter)">Edit</button>
          <button @click="deleteChapter(chapter.id)">Delete</button>
        </div>
        <button @click="showAddChapter = true">+ Chapter</button>
      </section>
    </main>
    <div v-if="showAddSubject" class="modal">
      <div class="modal-content">
        <h3>New Subject</h3>
        <input v-model="newSubject.name" placeholder="Name" />
        <input v-model="newSubject.description" placeholder="Description" />
        <button @click="addSubject">Save</button>
        <button @click="showAddSubject = false">Cancel</button>
      </div>
    </div>
    <div v-if="showAddChapter" class="modal">
      <div class="modal-content">
        <h3>New Chapter</h3>
        <input v-model="newChapter.name" placeholder="Name" />
        <input v-model="newChapter.description" placeholder="Description" />
        <button @click="addChapter">Save</button>
        <button @click="showAddChapter = false">Cancel</button>
      </div>
    </div>
  </div>
</template>
<script setup>
import { ref } from 'vue';
const subjects = ref([{ id: 1, name: 'Physics', description: 'Physics subject' },{ id: 2, name: 'Math', description: 'Math subject' }]);
const chapters = ref([{ id: 1, name: 'App Dev-1', description: 'App Dev chapter' },{ id: 2, name: 'CSS', description: 'CSS chapter' }]);
const searchQuery = ref('');
const showAddSubject = ref(false);
const showAddChapter = ref(false);
const newSubject = ref({ name: '', description: '' });
const newChapter = ref({ name: '', description: '' });
const logout = () => { alert('Logged out!'); };
const search = () => { alert('Searching for: ' + searchQuery.value); };
const addSubject = () => { if (newSubject.value.name) { subjects.value.push({ ...newSubject.value, id: Date.now() }); newSubject.value = { name: '', description: '' }; showAddSubject.value = false; } };
const addChapter = () => { if (newChapter.value.name) { chapters.value.push({ ...newChapter.value, id: Date.now() }); newChapter.value = { name: '', description: '' }; showAddChapter.value = false; } };
const editSubject = (subject) => { alert('Edit subject: ' + subject.name); };
const deleteSubject = (id) => { subjects.value = subjects.value.filter(s => s.id !== id); };
const editChapter = (chapter) => { alert('Edit chapter: ' + chapter.name); };
const deleteChapter = (id) => { chapters.value = chapters.value.filter(c => c.id !== id); };
</script>
<style scoped>
.admin-dashboard { min-height: 100vh; background: #f5f5f5; }
.admin-header { display: flex; justify-content: space-between; align-items: center; padding: 1rem 2rem; background-color: #2c3e50; color: white; }
.main-nav { display: flex; gap: 1.5rem; }
.main-nav a { color: white; text-decoration: none; font-weight: 500; }
.main-nav a.router-link-exact-active { color: #42b983; }
.logout-btn { background-color: #e74c3c; color: white; border: none; padding: 0.5rem 1rem; border-radius: 4px; cursor: pointer; margin-left: 1rem; }
.admin-search { display: flex; gap: 0.5rem; }
.admin-search input { padding: 0.5rem; border-radius: 4px; border: 1px solid #ddd; }
.welcome-message { font-weight: bold; }
.dashboard-content { display: flex; gap: 2rem; padding: 2rem; }
.subject-section, .chapter-section { background: #fff; padding: 1rem; border-radius: 8px; flex: 1; min-width: 300px; }
.subject-card, .chapter-card { border: 1px solid #ddd; border-radius: 4px; padding: 0.5rem; margin-bottom: 0.5rem; display: flex; flex-direction: column; gap: 0.25rem; }
button { margin-top: 0.5rem; }
.modal { position: fixed; top: 0; left: 0; right: 0; bottom: 0; background: rgba(0,0,0,0.3); display: flex; align-items: center; justify-content: center; }
.modal-content { background: #fff; padding: 2rem; border-radius: 8px; min-width: 300px; display: flex; flex-direction: column; gap: 1rem; }
</style>